p;\n  src: url(https://fonts.gstatic.com/s/stick/v9/Qw3TZQpMCyTtJSvf.ttf) format('truetype');\n}\n","@import \"variables/variables.less\";\n@import \"./start_screen\";\n@import \"./play\";\n@import \"./options\";\n@import \"./best_scores\";\n@import \"./end_screen\";\n@import \"./animations\";\n\nhtml{\n    height: 100%;\n}\nbody{\n    background-color: green;\n    background-image: url(../images/background-forest.gif);\n    background-size: cover;\n    background-repeat: no-repeat;\n    color: white;\n    font-family: 'Stick', sans-serif;\n}\na{\n    color: inherit;\n    text-decoration: none;\n    &:hover{\n        color: gold;\n    }\n}\n.container{\n    .center();\n    .border(11px, black);\n    overflow: hidden;\n    width: 1100px;\n    height: 800px;\n}\n.panel{\n    width: 100%;\n    height: 100%;\n}\n.game{\n    .center();\n    height: 100%;\n    background-image: url(../images/wooden.jpg);\n    background-size: cover;\n    background-repeat: no-repeat;\n    width: 100%;\n    height: 100%;\n}\n#overlay{\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    background-color: transparent;\n    z-index: 9999;\n    display: none;\n}\n\n@media @mobile_landscape {\n    .container{\n        top: 50%;\n        width: 65%;\n        max-height: 95%;\n    }\n}\n\n@media @mobile_portrait {\n    .container{\n        top: 40%;\n        width: 95%;\n        height: 900px;\n    }\n}","@font-face {\n  font-family: 'Indie Flower';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/indieflower/v12/m8JVjfNVeKWVnh3QMuKkFcZlbg.ttf) format('truetype');\n}\n","@font-face {\n  font-family: 'RocknRoll One';\n  font-style: normal;\n  font-weight: 400;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/rocknrollone/v7/kmK7ZqspGAfCeUiW6FFlmEC9guU.ttf) format('truetype');\n}\n","@font-face {\n  font-family: 'Orbitron';\n  font-style: normal;\n  font-weight: 700;\n  font-display: swap;\n  src: url(https://fonts.gstatic.com/s/orbitron/v19/yMJMMIlzdpvBhQQL_SC3X9yhF25-T1ny_Cmxpg.ttf) format('truetype');\n}\n","@import \"./fonts.less\";\n@import \"./colors.less\";\n@import \"./borders.less\";\n\n.center(){\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n}\n\n.buttons{\n    position: absolute;\n    width: 100%;\n    justify-content: space-around;\n    bottom: 10%;\n    right: 0;\n    text-align: center;\n    button{\n        .button;\n        font-size: 1.2em;\n    }\n}\n.button{\n    display: inline-block;\n    background-color: #colors[@optionAndEnd-button-background-color];\n    padding: 10px 30px 15px;\n    border-radius: 22px;\n    color: white;\n    font-size: 2.5rem;\n    border: none;\n    outline: none;\n    font-family: inherit;\n    min-width: 250px;\n    &:hover{\n        cursor: pointer;\n        background-color: #colors[@optionAndEnd-button-hover-background-color];\n    }\n}\n\n\n@mobile_landscape: ~\"all and (orientation: landscape) and (max-device-width: 800px)\";\n@mobile_portrait: ~\"all and (orientation: portrait) and (max-device-width: 800px)\";","\n.door(){\n    position: absolute;\n    z-index: 1000;\n    width: 100%;\n    height: 100%;\n    background-size: cover;\n    .border(1px, black); \n    display: inline-block;\n    margin: auto;\n}\n@keyframes closeTheDoorLeft{\n    from{\n        left: -100%\n    }\n    to{\n        left: -45%\n    }\n}\n@keyframes closeTheDoorRight{\n    from{\n        right: -100%\n    }\n    to{\n        right: -45%\n    }\n}\n@keyframes openTheDoorLeft{\n    from{\n        left: -45%\n    }\n    to{\n        left: -100%\n    }\n}\n@keyframes openTheDoorRight{\n    from{\n        right: -45%\n    }\n    to{\n        right: -100%\n    }\n}\n#left_door{\n    .door;\n    background-color: black;\n    left: -100%;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 90% (100%/9*5), 100% (100%/9*6),\n    90% (100%/9*7), 100% (100%/9*8),\n    90% 100%, 0 100%);\n}\n#left_door::before{\n    content: \"\";\n    .door;\n    right: 10px;\n    z-index: 999;\n    clip-path: polygon(0 0, 100% 0, \n    90% (100%/9*1), 100% (100%/9*2), 90% (100%/9*3),\n    100% (100%/9*4), 90% (100%/9*5), 100% (100%/9*6),\n    90% (100%/9*7), 100% (100%/9*8),\n    90% 100%, 0 100%);\n    background-image: url(../images/door.jpg);\n}\n#right_door{\n    .door;\n    background-color: black;\n    right: -100%;\n    clip-path: polygon(10% 0, 100% 0,\n    100% 100%, 0 100%, \n    10% (100%/9*8), 0 (100%/9*7), 10% (100%/9*6),\n    0 (100%/9*5), 10% (100%/9*4), 0% (100%/9*3),\n    10% (100%/9*2), 0 (100%/9*1)\n    );\n}\n#right_door::before{\n    content: \"\";\n    .door;\n    width: 97%;\n    left: 10px;\n    z-index: 999;\n    clip-path: polygon(10% 0, 100% 0,\n    100% 100%, 0 100%, \n    10% (100%/9*8), 0 (100%/9*7), 10% (100%/9*6),\n    0 (100%/9*5), 10% (100%/9*4), 0% (100%/9*3),\n    10% (100%/9*2), 0 (100%/9*1)\n    );\n    background-image: url(../images/door.jpg);\n}",".border(@width, @color, @radius: 0){\n    border: @width solid @color;\n    border-radius: @radius;\n}","@import \"./door.less\";\n\n.header-font{\n    color: #colors[@header-font-color];\n    letter-spacing: 5px;\n}\n#game_start{\n    text-align: center;\n    font-size: 1.5em;\n    opacity: .9;\n    width: percentage(.7);\n    margin: auto;\n    padding-top: 30px;\n    header{\n        .header-font;\n        & *{\n            margin: 4px auto;\n        }\n    }\n    section.menu_buttons{\n        margin: 90px 0;\n        div{\n            margin: 50px auto;\n            button.menu_button{\n                padding: 20px;\n                background-color: #colors[@main-button-background-color];\n                color: #colors[@main-button-font-color];\n                width: 70%;\n                transform: skew(-15deg);\n                font-size: 1.5rem;\n                font-family: inherit;\n                cursor: pointer;\n                span{\n                    display: inline-block;\n                    transform: skew(15deg);\n                }\n                &.active{\n                    background-color: #colors[@main-button-hover-background-color];\n                    color: #colors[@main-button-hover-font-color];\n                }\n            }\n        }\n        \n    }\n    .switch_sound{\n        position: absolute;\n        bottom: 30px;\n        right: 50px;\n        background-color: #colors[@switchsound-button-background-color];\n        padding: 10px 10px 5px;\n        cursor: pointer;\n        img{\n            width: 32px;\n        }\n    }\n    \n    \n}\n\n@media @mobile_landscape{\n    #game_start{\n        header{\n            font-size: 15px;\n        }\n        section.menu_buttons{\n            margin: 30px 0;\n            div{\n                margin: 30px 0;\n                & .menu_button{\n                    font-size: 1.1rem !important;\n                    width: 90%;\n                }\n            }\n            \n        }\n    }\n}\n@media @mobile_portrait{\n    #game_start{\n        header{\n            font-size: 1.2em;\n        }\n        section.menu_buttons{\n            div{\n                & .menu_button{\n                    font-size: 1.9rem !important;\n                    width: 90%;\n                }\n            }\n           \n        }\n        .switch_sound{\n            img{\n                width: 64px;\n            }\n        }\n    }\n}","#game_board{\n    width: 100%;\n    height: 100%;\n    position: relative;\n    #game_cards{\n        width: 80%;\n        .center();\n        text-align: center;\n        .card_reverse{\n            display: inline-block;\n            margin: 5px;\n            .border(5px, black, 5px);\n            box-sizing: border-box;\n            text-align: center;\n            transition: all .3s linear;\n            filter: brightness(70%);\n            cursor: pointer;\n    